import React, { Component } from "react";
import BaseLayout from "./base";
import Banner from "./banner";
import Footer from "./footer";
import {
	Button,
	Container,
	Divider,
	Grid,
	Header,
	Icon,
	Image,
	List,
	Menu,
	Responsive,
	Segment,
	Sidebar,
	Label
} from "semantic-ui-react";
import config from "./config";

const NavBarMobile = ({
	children,
	leftItems,
	onPusherClick,
	onToggle,
	rightItems,
	visible,
	banner
}) => (
	<Sidebar.Pushable>
		<Sidebar
			as={Menu}
			animation="overlay"
			icon="labeled"
			inverted
			items={rightItems}
			vertical
			visible={visible}
			style={{ minWidth: "50vw" }}
			onClick={onToggle}
		/>
		<Sidebar.Pusher
			dimmed={visible}
			onClick={onPusherClick}
			style={{ minHeight: "100vh" }}
		>
			<Segment
				inverted
				textAlign="center"
				style={{
					minHeight: banner ? "450px" : 0,
					padding: banner ? "1em 0em" : 0
				}}
				vertical
			>
				<Container>
					<Menu inverted secondary size="large">
						<Menu.Item onClick={onToggle}>
							<Icon name="sidebar" />
						</Menu.Item>
						<Menu.Item as="a" header href="/">
							<Image
								size="mini"
								src="/static/img/icon.png"
								style={{ marginRight: "0.5em" }}
							/>
						</Menu.Item>
						<Menu.Menu position="right">
							{leftItems.map(item => (
								<Menu.Item {...item} />
							))}
						</Menu.Menu>
					</Menu>
				</Container>
				{banner ? <Banner mobile /> : ""}
			</Segment>
			<Segment
				vertical
				style={{
					padding: "2em 0em"
				}}
			>
				{children}
			</Segment>
		</Sidebar.Pusher>
	</Sidebar.Pushable>
);

const NavBarDesktop = ({ children, leftItems, rightItems, banner }) => (
	<div style={{ minHeight: "100vh" }}>
		<Segment
			inverted
			textAlign="center"
			style={{
				minHeight: banner ? "700px" : 0,
				padding: banner ? "0em 0em" : 0
			}}
			vertical
		>
			<Menu inverted secondary size="large">
				<Container>
					<Menu.Item as="a" header href="/">
						<Image
							size="mini"
							src="/static/img/icon.png"
							style={{ marginRight: "0.5em" }}
						/>{" "}
						<span className="yo-light-title">YOPO</span>
					</Menu.Item>
					{leftItems.map(item => (
						<Menu.Item {...item} />
					))}
					<Menu.Menu position="right">
						{rightItems.map(item => (
							<Menu.Item {...item} />
						))}
					</Menu.Menu>
				</Container>
			</Menu>
			{banner ? <Banner /> : ""}
		</Segment>
		<Segment
			vertical
			style={{
				padding: "1em"
			}}
		>
			{children}
		</Segment>
	</div>
);

class NavBar extends Component {
	state = {
		visible: false
	};

	handlePusher = () => {
		const { visible } = this.state;
		if (visible) this.setState({ visible: false });
	};

	handleToggle = () => this.setState({ visible: !this.state.visible });

	render() {
		const { children, leftItems, rightItems, banner } = this.props;
		const { visible } = this.state;

		return (
			<div>
				<Responsive name="mobile" {...Responsive.onlyMobile}>
					<NavBarMobile
						leftItems={leftItems}
						onPusherClick={this.handlePusher}
						onToggle={this.handleToggle}
						rightItems={rightItems}
						visible={visible}
						banner={banner}
					>
						{children}
					</NavBarMobile>
				</Responsive>
				<Responsive
					name="desktop"
					minWidth={Responsive.onlyTablet.minWidth}
				>
					<NavBarDesktop
						leftItems={leftItems}
						rightItems={rightItems}
						banner={banner}
					>
						{children}
					</NavBarDesktop>
				</Responsive>
			</div>
		);
	}
}

const leftItems = [
	{ as: "a", content: "Home", key: "home", href: "/" },
	{ as: "a", content: "Investment", key: "invest", href: "/#invest" }
];
const rightItems = [
	{ as: "a", content: "Instrution", key: "instrution", href: "/#instrution" },
	{ as: "a", content: "Tech", key: "tech", href: "/#tech" },
	{ as: "a", content: "FAQ", key: "faq", href: "/#faq" },
	{
		as: "a",
		content: "Smart Contract",
		key: "smartcontract",
		href: `${config.codeUrl()}`,
		target: "_blank"
	},
	{
		as: "a",
		content: <Label color="blue" size="large">Promo</Label>,
		key: "promo",
		href: `/promo`
	}
];

class Layout extends Component {
	render() {
		return (
			<BaseLayout>
				<NavBar
					leftItems={leftItems}
					rightItems={rightItems}
					banner={this.props.banner}
				>
					{this.props.children}
				</NavBar>
				<Footer />
			</BaseLayout>
		);
	}
}

export default Layout;
